
.wrapper-social-links{
   display: flex;
   align-items: center;
   column-gap: 40px;
}
.contact-link{
   font-size: 18px;
   font-weight: 600;
   display: flex;
   align-items: center;
   background-size: cover;
   background-repeat: no-repeat;
   column-gap: 10px;
   
   &__holder-ico{
      display: inline-block;
      width: 40px;
      height: 40px;
   }

   @media (max-width: 1440px) {
      
      &__holder-text{
         display: none;
      }
      
      &__holder-ico{
         display: inline-block;
         width: 30px;
         height: 30px;
      }
      
   }

}

.context-link{
   color: $colorContextLink !important;
   font-size: 20px;
   line-height: 1.08;
   text-decoration: underline !important;
   text-underline-offset: 5px;

      @media (max-width: 1199px) {
         font-size: 16px;
         line-height: 1.05;
      }

      @media (max-width: 1024px) {
       font-size: 16px;
      }

      @media (max-width: 768px) {
         font-size: 14px;
      }
}

@media(min-width:1025px){
   .header a:hover,
   .about-us a:hover,
   .contacts a:hover,
   .footer a:hover{
      opacity: 0.7;
   }
}